.intro {
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;



    .left {
        flex: 0.5;
        overflow: hidden;
        align-items: center;

        .imageContainer{
            width: 700px;
            height: 600px;
            background-color: crimson;
            border-radius: 50%;
            display: flex;
            align-items: flex-end;
            justify-content: center;
            float: right;

            img{
                height: 90%;
            }
        }
    }
    .right {
        flex: 0.5;
        

        .wrapper {
            width: 100%;
            height: 100%;
            padding-left: 50px;
            display: flex;
            flex-wrap: wrap;
            flex-direction: column;
            justify-content: center;

            h1{
                font-size: 60px;
                margin: 10px 0;
            }
            h2{
                font-size: 35px;
            }
            h3{
                font-size: 30px;

                span{
                    font-size: inherit;
                    color:crimson;
                }
                .ityped-cursor{
                    animation: blink 1s infinite;
                }

                @keyframes blink {
                    50%{
                        opacity: 1;
                    }
                    100%{
                        opacity: 0;
                    }
                }
            }
        }
        a{
            position: absolute;
            bottom: 10px;
            
            img{
                width: 30px;
                animation: arrowBLink 2s infinite;
            }
            @keyframes arrowBLink {
                100%{
                    opacity: 0;
                }
            }
        }
    }
}